import React, { Component, Suspense } from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import Footer from './components/Footer/Footer';
import { Route, withRouter, HashRouter } from 'react-router-dom';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import Vue from './components/Vue/Vue';
import Angular from './components/Angular/Angular';
import Jquery from './components/Jquery/Jquery';
import UsersContainer from './components/Users/UsersContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import LoginPage from './components/Login/Login';
import { connect } from 'react-redux';
import { initialaizeApp } from '././Redux/app-reducer';
import { compose } from 'redux';
import Preloader from './components/common/Preloader/Preloader';
import { BrowserRouter } from 'react-router-dom';
import store from './Redux/redux-store';
import { Provider } from 'react-redux';
import { withSuspenseHoc } from './HOC/withSuspenseHoc';

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));




class App extends Component {

  componentDidMount() {
    this.props.initialaizeApp();
  }

  render() {
    if (!this.props.initialaized) {
      return <Preloader />
    }

    return (
      <div className='app-wrapper'>
        <HeaderContainer />
        <Navbar />
        <div className='app-wrapper-content'>
          <Route path="/dialogs"
            render={withSuspenseHoc(DialogsContainer)}/>
          <Route path='/profile/:userId?'
            render={withSuspenseHoc(ProfileContainer)}/>

          <Route path='/users'
            render={() => <UsersContainer />} />
          <Route path='/login'
            render={() => <LoginPage />} />

          <Route path="/News" render={() => <News />} />
          <Route path="/Music" render={() => <Music />} />
          <Route path="/Settings" render={() => <Settings />} />
          <Route path="/Vue" render={() => <Vue />} />
          <Route path="/Angular" render={() => <Angular />} />
          <Route path="/Jquery" render={() => <Jquery />} />


        </div>
        <Footer />
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  initialaized: state.app.initialaized

})

let AppContainer = compose(withRouter, connect(mapStateToProps, { initialaizeApp }))(App);


const SamuraiJsApp = (props) => {
  return <HashRouter>
    <Provider store={store}>
      <AppContainer />
    </Provider>
  </HashRouter>
}

export default SamuraiJsApp;