const ADD_MESSAGE = 'ADD-MESSAGE'


let initialState = {
    dialogs: [
        { id: 1, name: 'Khoren' },
        { id: 2, name: 'Anush' },
        { id: 3, name: 'Areg' },
        { id: 4, name: 'Angelina' },
        { id: 5, name: 'Alvinka' },
        { id: 6, name: 'Jon' }
    ],
    messages: [
        { id: 1, message: 'Hi' },
        { id: 2, message: 'How' },
        { id: 3, message: 'Yo' },
        { id: 4, message: 'Hi' },
        { id: 5, message: 'How' },
        { id: 6, message: 'Yo' }
    ],
    
}

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_MESSAGE: 
            let newMessage = action.newMessage;
            return {
                ...state,
                messages: [...state.messages, { id: 7, message: newMessage }]
            };

        default:
            return state;

    }

}
export const addMessageCreator = (newMessage) => ({ type: ADD_MESSAGE, newMessage })


export default dialogsReducer;