import React from 'react';
import s from './Navbar.module.css';
import { NavLink } from 'react-router-dom';

const Navbar = (props) => {
    return (
    <nav className={s.nav}>
        <div className={s.item}><NavLink  to="/profile"  activeClassName={s.activeLink}>Profile</NavLink></div>
        <div className={`${s.item} ${s.active}`}><NavLink to="/dialogs" activeClassName={s.activeLink}>Messages</NavLink></div>
        <div className={`${s.item} ${s.active}`}><NavLink to="/users" activeClassName={s.activeLink}>Users</NavLink></div>
        <div className={s.item}><NavLink to="/news">News</NavLink></div>
        <div className={s.item}><NavLink to="/music">Music</NavLink></div>
        <div className={s.item}><NavLink to="/settings">Settings</NavLink></div>
        

        <div className={s.friends}>
            <h1>FRIENDS</h1>
            <div className={s.friend}><NavLink to="/Vue"><img src="https://i.pinimg.com/originals/73/06/a1/7306a1a8d2009f2b78ff9cf354931be5.png" alt="avatar"/><span>Vue</span></NavLink></div>

            <div className={s.friend}><NavLink to="/Angular"><img src="https://res.cloudinary.com/teepublic/image/private/s--JnfxjOP1--/t_Resized%20Artwork/c_fit,g_north_west,h_1054,w_1054/co_ffffff,e_outline:53/co_ffffff,e_outline:inner_fill:53/co_bbbbbb,e_outline:3:1000/c_mpad,g_center,h_1260,w_1260/b_rgb:eeeeee/c_limit,f_jpg,h_630,q_90,w_630/v1509564403/production/designs/2016815_1.jpg" alt="avatar"/><span>Angular</span></NavLink></div>

            <div className={s.friend}><NavLink to="/Jquery"><img src="https://apprecs.org/gp/images/app-icons/300/c6/com.innovsystems.android.jqueryapi.jpg" alt="avatar"/><span>Jquery</span></NavLink></div>
            
        </div>
    </nav>
    )
}

export default Navbar;