import React from 'react';
import s from './Vue.module.css';

const Vue = (props) => {
    return (

        <div className={s.vue}>
            <div className={s.info}>
                <span>
                    Vue.js
                    <p>Age: 10</p>
                    <p>Date of Birth: 23.06.2014</p>
                    <p>Country: USA</p>
                </span>
                <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/95/Vue.js_Logo_2.svg/694px-Vue.js_Logo_2.svg.png" alt="" />
            </div>
            <div className={s.biography}>
                <span>Vue.js — JavaScript-фреймворк с открытым исходным кодом для создания пользовательских интерфейсов. Легко интегрируется в проекты с использованием других JavaScript-библиотек. Может функционировать как веб-фреймворк для разработки одностраничных приложений в реактивном стиле.</span>
            </div>

        </div>
    )
}

export default Vue;